import React, {Component} from 'react';
import {Panel, Col, Form, FormGroup, FormControl, ControlLabel, Button} from 'react-bootstrap';
import Layout from 'Layout/Layout';
import Config from 'Index/Config';
import Data from 'Index/Data';
let style;

class Out extends Component {
  constructor() {
    super();
    this.state = {
      auth: false,
      user: '',
      pass: ''
    }
    this.auth = this.auth.bind(this);
  }

  auth() {
    let self = this;
    window.server.authenticate({type: 'local', 'username': this.state.user, 'password': this.state.pass}).then(function(result) {
      console.log('Authenticated!', window.server.get('token'));
      self.setState({auth: true});
    }).catch(function(error) {
      console.error('Error authenticating!', error);
    });
  }

  render() {
    if (this.state.auth) {
      return (
        <Layout>
          <div style={style.content}>
            <Col md={3}>
              <Panel header="Números" bsStyle="primary">
                <Data/>
              </Panel>
            </Col>
            <Col md={9}>
              <Panel header="Configuración" bsStyle="primary">
                <Config/>
              </Panel>
            </Col>
          </div>
        </Layout>
      )
    } else {
      return (
        <Layout>
          <div style={style.content}>
            <Panel header="Login" bsStyle="primary">
              <Form horizontal>
                  <FormGroup>
                      <Col componentClass={ControlLabel} sm={2}>
                          Usuario
                      </Col>
                      <Col sm={10}>
                          <FormControl onChange={(event)=>{this.setState({user: event.target.value})}} type="text"/>
                      </Col>
                  </FormGroup>

                  <FormGroup>
                      <Col componentClass={ControlLabel} sm={2}>
                          Contraseña
                      </Col>
                      <Col sm={10}>
                          <FormControl onChange={(event)=>{this.setState({pass: event.target.value})}} type="password"/>
                      </Col>
                  </FormGroup>

                  <FormGroup>
                      <Col smOffset={1} sm={10}>
                          <Button onClick={this.auth}>
                              Login
                          </Button>
                      </Col>
                  </FormGroup>
              </Form>
            </Panel>
          </div>
        </Layout>
      )
    }
  }
}

export default Out;

style = {
  content: {
    marginLeft: '50%',
    transform: 'translateX(-50%)',
    marginTop: '6em',
    width: '80%',
    textAlign: 'center'
  }
}



